:root {
    --w-blue-color:#8729FF;
    --w-blue-color-h:#6C1ECE;
    --w-dark-blue-color:#1C1130;

    --w-pink:#ff00ff;

    --w-bg:#151722;
    --w-text-color:#72778F;
    --w-title-color:#ffffff;
    --w-border-color:#72778F;
    --w-border-color-h:var(--w-blue-color);
    --w-container-p: 0 20px;

    --w-bg-box:#22232F;
    --w-ff:24px;
    --w-fl:34px;
    --w-ffh1:34px;
    --w-flh1:44px;
    --w-th:0.2s;
    --w-br:12px;
    --w-mbh1:20px;
    --w-video-box: calc(50% - 16px);

    --w-play-bg:var(--w-blue-color);
    --w-play-i:var(--w-title-color);
    --w-play-bg-h:var(--w-blue-color-h);
    --w-play-i-h:var(--w-title-color);

    --w-bg-button:var(--w-blue-color);
    --w-color-button:#ffffff;
    --w-bg-button-h:var(--w-blue-color-h);
    --w-color-button-h:#ffffff;
    --w-br-button:12px;
}

@media (min-width: 768px) {
    :root {
        --w-ff:28px;
        --w-fl:38px;
        --w-ffh1:44px;
        --w-flh1:64px;
        --w-mbh1:30px;
        --w-container-p:0 30px;
        --w-video-box: calc(25% - 16px);
    }
}
@media (min-width: 1200px) {
    :root {
        --w-ff:32px;
        --w-fl:48px;

        --w-ffh1:64px;
        --w-flh1:80px;
        --w-mbh1:50px;
        --w-container-p:0 50px;
    }
}

html,
body {
	margin: 0;
	height: 100%;
	padding: 0;
	display: block;
	min-height: 100%;
	color: var(--w-text-color);
    background-color: var(--w-bg);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    line-height: 20px;
}

a {
    color: var(--w-pink);

    &:hover {
        text-decoration: none;
    }
}

.pink {
    color: var(--w-pink);
    font-weight: normal;
}

h1 {
    font-size: var(--w-ffh1);
    line-height: var(--w-flh1);
    text-align: center;
    nav-down: var(--w-mbh1);
    color: var(--w-title-color);
    margin: 0;
}

h2,
h3,
h4,
h5,
h6 {
    font-size: var(--w-ff);
    line-height: var(--w-fl);
    text-align: center;
    nav-down: var(--w-mbh1);
    color: var(--w-title-color);
}

h3 {
    text-align: left;
    margin: 0;
}

.small-title {
    color: var(--w-title-color);
}

#header {
    display: flex;
    align-items: center;
    justify-content: center;
    padding:20px 0;
    margin:0 0 50px;

    .logo-wfs {
        max-width: 200px;

        img {
            display: block;
            width: 100%;
            height: auto;
        }
    }

    .menu {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex: 1;
        margin: 0 0 0 20px;

        ul {
            margin: 0;
            padding: 0;
            list-style: none;
        }
    }
}

.wfsv-container {
	position: relative;
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	padding: var(--w-container-p);
	box-sizing: border-box;
}

.wfsv-list {
    padding: 0;
    margin: 0;

    li {
        margin-bottom: 10px;
    }
}

#footer {
    padding: 20px 0;
    p {
        text-align: center;
        margin: 0;
    }
}

.wfsv-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 20px;
    height: 40px;
    background: var(--w-bg-button);
    color: var(--w-color-button);
    border-radius: var(--w-br-button);
    transition: all var(--w-th) ease-in-out;
    text-decoration: none;

    &:hover {
        background: var(--w-bg-button-h);
        color: var(--w-color-button-h);
    }
}

.wfs-section {
    background: var(--w-bg-box);
    overflow: hidden;
    min-height: 60vh;
    padding: 30px;
    box-sizing: border-box;
}

.wfs-holder {
    margin-bottom: 50px;
}

.wfs-button {
    margin-bottom: 30px;
}

.go-to-top {
    position: fixed;
    bottom: -30px;
    right: 20px;
    width: 30px;
    height: 30px;
    border: 1px solid var(--w-text-color);
    background: var(--w-bg-box);
    transition: all var(--w-th) ease-in-out;

    &.evo-gototop-visible {
        bottom: 0;
    
    }

    path {
        stroke: var(--w-text-color);
        transition: bottom var(--w-th) ease-in-out;
    }

    &:hover {
        path {
            stroke: var(--w-pink);
        }
    }
}
.hidden-element {
    position: absolute;
    left: -9999px;
    top: -9999px;
}